body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.profile-avatar{
	width: 100%;
}
.content{
	padding: 50px 0px;
}
[class^="col-"] img {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-height: 500px;
}

.person-name {
  font-size: 55px;
}

.person-quote {
  font-style: italic;
  font-size: 20px;
  max-width: 80%;
}

.person-title {
  color: #dddddd  ;
}

.modal-dialog {
  max-width: 650px !important;
}

.carousel-caption > h3 {
  font-size: 18px;
  text-shadow: 0 0 2px black;
}

.carousel-caption > p {
  display: none !important;
}

.avatar{
	border-radius: 50%;
	margin-bottom: 20px;
	width: 100%;
}
.name{
	text-align: center;
}
